import React from 'react';
import MobileCheck from '../js/mobileCheck.js';

import LotteDeptPc from './LotteDeptPc';
import LotteDeptMo from './LotteDeptMo';

class LotteDept extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            isMobile : false
        }
    }

    componentWillMount(){
        MobileCheck(this);
    }

    render(){
        return (
            <React.Fragment>
                {this.state.isMobile ? <LotteDeptMo /> : <LotteDeptPc />}
            </React.Fragment>
        )
    }
}

export default LotteDept;