import React from 'react';

import { ViewTopCmp, NoticeBottomCmp } from '../component';

import '../css/project.scss';
import '../css/notice.scss';

import noticeData from './noticeData.json';

// image import
import imgVisua1 from '../images/notice/notice-news-adcapsuleopen-img.jpg';

class NoticeView1 extends React.Component {
    constructor(props){
        super(props);

        // json 파일에서 가져올 리스트 번호
        this.idx = 1;
        this.state = {
            type: noticeData[this.idx].type,        //project type
            cate: noticeData[this.idx].cate,        //sorting category / 카테고리의 클래스를 받아 sorting
            title: noticeData[this.idx].title,      //title
            desc: noticeData[this.idx].desc,        //description
            date: noticeData[1].date,               //date
            img:imgVisua1,                          //visual image
        }
    }

    render(){
        return (
            <React.Fragment>

                <div className="notice_view">
                    <ViewTopCmp
                        type={this.state.type} 
                        title={this.state.title} 
                        desc={this.state.desc} 
                    />

                    <div className="inner_left viewcont">

                        <div className="visual">
                            <img src={this.state.img} alt=""/>
                        </div>
                            
                        <p>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                            Notice View Contents <br/>
                        </p>
                        
                    </div>

                   
                    <NoticeBottomCmp/>

                </div>

            </React.Fragment>
        )
    }
}

export default NoticeView1;