import React from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';

import { ViewTopCmp, PrjInfoCmp, PrjBottomCmp } from '../component';

import '../css/project.scss';

// project style import
import '../css/counselor.scss';

import projectData from './projectData.json';

// image import
import img1 from '../images/project/counselor/m-img-pc-main.png';
import img2 from '../images/project/counselor/m-img-phone-main.png';
import img3 from '../images/project/counselor/m-img-phone-01.png';
import img4 from '../images/project/counselor/m-img-person-01.png';
import img5 from '../images/project/counselor/m-img-phone-02.png';
import img6 from '../images/project/counselor/m-img-person-02.png';
import img7 from '../images/project/counselor/m-img-txt-01.png';
import img8 from '../images/project/counselor/m-img-templet-01.png';
import img9 from '../images/project/counselor/m-img-phone-03.png';
import img10 from '../images/project/counselor/m-img-person-03.png';
import img11 from '../images/project/counselor/m-img-txt-02.png';
import img12 from '../images/project/counselor/m-img-templet-02.png';
import img13 from '../images/project/counselor/img-txt-03.png';

class CounselorMo extends React.Component {
    constructor(props){
        super(props);

        this.idx = 1;

        this.state = {
            type: projectData[this.idx].type,
            cate: projectData[this.idx].cate,
            title: projectData[this.idx].title,
            desc: projectData[this.idx].desc,
            mission: projectData[this.idx].mission,
            partner: projectData[this.idx].partner,
            launch: projectData[this.idx].launch,
            url: projectData[this.idx].url     
        }
    }

    componentDidMount(){
        AOS.init({});
    }

    render(){
        return (
            <React.Fragment>
                
                <ViewTopCmp
                    type={this.state.type} 
                    cate={this.state.cate}
                    title={this.state.title} 
                    desc={this.state.desc} 
                />
                
                <div className="counselorMoWrap">
                    <div className="visualTop">
                        <h2><span className="shape1" data-aos="flip-left" data-aos-anchor-placement="top-bottom" data-aos-duration="9000" data-aos-easing="ease-in-out"></span>Amore Counselor<span className="shape2" data-aos="flip-right" data-aos-duration="9000" data-aos-easing="ease-in-out"></span></h2>
                        <p>RESIPONSIVE WEB PROJECT</p>
                        <div className="visual">
                            <div className="img1" data-aos="zoom-in-right"><img src={img1} alt=""/></div>
                            <div className="img2" data-aos="zoom-in-left"><img src={img2} alt=""/></div>
                        </div>

                        <div className="info">
                            <div className="txtArea">
                                <div>Challenge 
                                    <p>아시안뷰티를 고객들에게 전달하는 아모레 카운셀러는 아모레퍼시픽의 긴 역사와 함께했으며 현재에도 활발하게 활동하고 있습니다.</p>
                                    <p>우리는 카운셀러들의 다양한 콘텐츠와 정보를 한눈에 담아내는 독자적인 플랫폼을 구축하기위해 ‘꿈을 이룬 당당한 여성들’이라는 콘셉트가 드러날 수 있도록 제작 방향을 설정했습니다.</p>
                                    <p>보편적인 그리드 형태는 많은 양의 콘텐츠를 보여주기에 효과적일 수는 있으나 독자적인 아이덴티티를 표현하기엔 아쉬웠습니다.</p>
                                    <p>아모레카운셀러만의 독자적인 아이덴티티를 표현하기위해 자유로운 그리드 형태와 생동감있는 UI, 신선하고 감각적인 디자인으로 PC/모바일 환경에 최적화 된 서비스를 구현하였습니다.</p>
                                </div>
                                <div>
                                    <div>Partner <p>AMORE PACIFIC</p></div>
                                    <div>Date <p>2018.07</p></div>
                                </div>
                                <div>
                                    <div>Mission <p>아모레 카운셀러 공식사이트 구축</p></div>
                                    <div>LINK <p>http://www.amorecounselor.com</p></div>
                                </div>
                            </div>
                            <span className="shape3" data-aos="fade-up"></span>
                            <div className="moArea" data-aos="zoom-in">
                                <div><img src={img3} alt="" /></div>
                                <div><img src={img5} alt="" /></div>
                                <div><img src={img9} alt="" /></div>
                                <div><img src={img5} alt="" /></div>
                            </div>
                        </div>
                    </div>

                    <div className="banner01">
                        <div data-aos="fade-down"><img src={img7} alt="" /></div>
                        <div data-aos="fade-up-left"><img src={img8} alt="" /></div>
                    </div>

                    <div id="main_story01" className="parallax animate">
                        <div className="box_table">
                            <div className="box_tablecell">
                                <div className="clearfix">
                                    <div className="fl txt_story_pers01">
                                        <div className="inner">
                                        <h2 className="tit02 type2"><span className="line_top">OUR</span> STORY<em className="shape5" data-aos="flip-left" data-aos-duration="9000" data-aos-easing="ease-in-out"></em></h2>
                                        <p className="txt01">I’m a <br />career <br />woman</p>
                                        <p className="txt02">경력단절녀,<br />카운셀러로<br />새 출발!</p>
                                        <div className="box_btn"><span>자세히보기</span><em className="shape4" data-aos="flip-left" data-aos-duration="9000" data-aos-easing="ease-in-out"></em></div>
                                        </div>
                                    </div>
                                    <div className="fl img_story_pers01">
                                        <div className="inner">
                                            <div className="inner_img">
                                                <em className="shape6"></em>
                                                <img src={img4} alt="" />
                                                <em className="shape7" data-aos="flip-left" data-aos-duration="9000" data-aos-easing="ease-in-out"></em>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="item02"  data-aos="fade-left" data-aos-duration="9000" data-aos-easing="ease-in-out"></div>
                    </div>

                    <div id="main_story02" className="parallax">
                        <div className="box_table">
                            <div className="box_tablecell">
                                <h2 className="tit02 type2"><span className="line_top">OUR</span> <span>STORY</span></h2>
                                <div className="clearfix">
                                    <div className="fl img_story_pers02">
                                        <div className="inner_img">
                                            <img src={img6} alt="" />
                                        </div>
                                    </div>
                                    <div className="fl txt_story_pers02">
                                        <div className="inner">
                                            <div className="inner_txt">
                                                <img src={img13} alt="Working mom 아이둘 전업맘 카운셀러 되다" />
                                            </div>
                                            <div className="box_btn"><span>자세히보기</span></div>
                                        </div>
                                    </div>
                                </div>
                                <div className="txtImg"><img src={img11} alt="dreams come true"/></div>
                            </div>
                        </div>
                        <div className="item01"></div>
                    </div>

                    <div id="main_story03" className="parallax">
                        <div className="box_table">
                            <div className="box_tablecell clearfix">
                                <div className="clearfix" data-aos="zoom-in-left">
                                    <span className="shape8" data-aos="flip-left" data-aos-duration="9000" data-aos-easing="ease-in-out"></span>
                                    <div className="fl txt_story_pers03">
                                        <h2 className="tit02 type2"><span className="line_top">OUR</span> STORY</h2>
                                        <p className="txt01">첫번째 꿈,<br />엄마의 병원비</p>
                                        <p className="txt02">처음으로 저를 위한 두 번째<br />꿈을 꾸기 시작했어요.</p>
                                        
                                        <div className="box_btn"><span>자세히보기</span></div>
                                    </div>
                                    <div className="fl img_story_pers03">
                                        <img src={img10} alt="" className="slideleft" />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="item01"></div>
                    </div>
                    <div className="banner02">
                        <div data-aos="fade-up-left"><img src={img12} alt="" /></div>
                    </div>
                </div>

            <PrjBottomCmp/>

            </React.Fragment>
        )
    }
}

export default CounselorMo;