import React from 'react';

import { ViewTopCmp, PrjInfoCmp, PrjBottomCmp } from '../component';

import '../css/project.scss';

import projectData from './projectData.json';

// image import
import imgVisua1 from '../images/work/ourwork-aritaum-img@2x.jpg';
import prjImg1 from '../images/work/prj_img1.jpg';
import prjImg2 from '../images/work/prj_template1.jpg';

class Template1 extends React.Component {
    constructor(props){
        super(props);

        // json 파일에서 가져올 리스트 번호
        this.idx = 7;
        this.state = {
            type: projectData[this.idx].type,        //project type
            cate: projectData[this.idx].cate,        //sorting category / 카테고리의 클래스를 받아 sorting
            title: projectData[this.idx].title,      //title
            desc: projectData[this.idx].desc,        //description
            mission: projectData[this.idx].mission,  //mission
            partner: projectData[this.idx].partner,  //partner
            launch: projectData[this.idx].launch,    //launch
            url: projectData[this.idx].url,          //url
            img:imgVisua1,                           //visual image
        }
    }

    render(){
        return (
            <React.Fragment>

                <div className="template">
                
                    <ViewTopCmp
                        type={this.state.type} 
                        title={this.state.title} 
                        desc={this.state.desc} 
                    />

                    <div className="inner_left">
                        <div className="visual">
                            <img src={this.state.img} alt=""/>
                        </div>
                    </div>

                    <div className="content inner_left">
                            
                        <div className="view_coltype">
                            <div className="left">
                                <p className="tit">challenge</p>
                            </div>
                            <div className="right">
                                <p className="desc">
                                    뉴스룸은 심플(simple 복잡하지 않고 쉬운)과 Clarifed(명확한)를 콘셉트로한 현대카드ㆍ현대캐피탈의 공식 홍보채널로 
                                    급변하는 디지털 환경에 적극적으로 대응하고 미디어에 정확하고 깊이있는 정보들을 빠르게 제공하기 위한 
                                    블로그 형태의 온라인 플랫폼입니다. 현대카드의 Tone &amp; Manner를 유지하여 일관된 브랜드 경험을 느끼게하고, 
                                    콘텐츠에만 집중할 수 있는 UX/UI로 현대카드ㆍ현대캐피탈이 미디어와 원활한 커뮤니케이션을 할 수있는 긍정적인 
                                    공간으로 느껴지게 하는데 초점을 맞추었습니다.
                                </p>
                                
                                <PrjInfoCmp 
                                    mission={this.state.mission}
                                    partner={this.state.partner}
                                    launch={this.state.launch}
                                    url={this.state.url}
                                />
                            </div>
                        </div>
                        
                    </div>

                    
                    <div className="thumb_set">
                        <img src={prjImg1} alt=""/>
                    </div>   

                    <div className="descbox">
                        <p>부가 설명 영역</p>
                        <b>원스톱(One-Stop) 서비스 실현</b>
                        <p>한국전력공사는 지금까지 확실한 브랜드 이미지로 사람들에게 다가왔습니다. ‘세상에 빛을, 이웃에 사랑을’이란 슬로건을 통해 사회공헌 이미지를 높였고, ‘세계로 향하는 KEPCO’를 통해서는 글로벌 기업 이미지를 강조했습니다. 이번 프로젝트는 기존 아이덴티티를 유지하는 것을 넘어 한국전력공사에 대한 사용자의 접근성을 높이는 데 주력하였습니다. </p>
                        <p>50여 개의 한국전력공사 대외 웹사이트를 통합하며 콘텐츠, 정보구조, 회원, 디자인 등 전반적인 부분이 하나가 됐습니다. HTML5와 CSS, 자바스크립트, 제이쿼리로 구조/표현/동작을 분리해 웹 표준 작업을 강화했습니다. 더불어 무분별한 플래시 콘텐츠는 제거해 일반 콘텐츠와 제이쿼리로 대체하고 대체 텍스트와 키보드 포커스를 제공하며 접근성도 높였습니다.</p>
                        <p>전자정부 프레임워크 기반의 CBD 개발방법론을 적용, 정부 가이드를 준수한 점도 이번 개편의 큰 변화입니다. 이제 사용자는 한국전력공사에 관한 원하는 정보는 이곳, 한국전력공사 대외 웹사이트 방문 한 번으로 얻을 수 있게 됐습니다.</p>
                    </div>

                    <div className="imgbox">
                        <img src={prjImg2} alt=""/>
                    </div>

                    <PrjBottomCmp/>

                </div>

            </React.Fragment>
        )
    }
}

export default Template1;