import React, { Component } from 'react';
import { NoticeListCmp } from '../component';

import Isotope from 'isotope-layout';
import imagesLoaded from 'imagesloaded';

// image import
import img1 from '../images/notice/notice-news-adcapsuleopen-img.jpg';
import img2 from '../images/notice/notice-awards-reddotaward-img.jpg';

import noticeList from '../notice/noticeData.json';


class Notice extends Component {
  constructor(props){
    super(props);
    this.state = {
        listData: [
            {
                type: noticeList[1].type,
                title: noticeList[1].title,
                cate: noticeList[1].cate,
                date: noticeList[1].date,
                img:img1,
                routeLink: 'NoticeView1'
            },
            {
                type: noticeList[2].type,
                title: noticeList[2].title,
                cate: noticeList[2].cate,
                date: noticeList[2].date,
                img:img2,
                routeLink: 'NoticeView2'
            },
            {
                type: noticeList[3].type,
                title: noticeList[3].title,
                cate: noticeList[3].cate,
                date: noticeList[3].date,
                img:img1,
                routeLink: 'NoticeView3'
            },
            {
                type: noticeList[4].type,
                title: noticeList[4].title,
                cate: noticeList[4].cate,
                date: noticeList[4].date,
                img:img2,
                routeLink: 'NoticeView4'
            },
            {
                type: noticeList[5].type,
                title: noticeList[5].title,
                cate: noticeList[5].cate,
                date: noticeList[5].date,
                img:img1,
                routeLink: 'NoticeView5'
            },
            {
                type: noticeList[6].type,
                title: noticeList[6].title,
                cate: noticeList[6].cate,
                date: noticeList[6].date,
                img:img2,
                routeLink: 'NoticeView6'
            },
            {
                type: noticeList[7].type,
                title: noticeList[7].title,
                cate: noticeList[7].cate,
                date: noticeList[7].date,
                img:img1,
                routeLink: 'NoticeView7'
            },
            {
                type: noticeList[8].type,
                title: noticeList[8].title,
                cate: noticeList[8].cate,
                date: noticeList[8].date,
                img:img2,
                routeLink: 'NoticeView8'
            },
        ],
        isLoaded: false,
        filterOpen: true,
        isMobile: false,
    }

  }


  //Loading ing
  componentDidMount(){

    // page scroll top
    window.scrollTo({'top':0});

    // isotope안에서 state변경 위함
    // isotope안에서 this가 바뀌므로 this를 _this에 저장해서 사용
    let _this = this;

    // Isotope js
    let grid = document.querySelector('.item_wrap');

    // imagesLoaded / Detect when images have been loaded.
    imagesLoaded(grid, function(){
        console.log('loaded');

        // 로드되는 시간 동안 로딩바 노출
        // _this.setState({
        //     isLoaded: !_this.state.isLoaded
        // });

        let iso = new Isotope( grid, {
            itemSelector: '.item_card',

            percentPosition: true,
            masonry:{
                columnWidth: '.grid-sizer',
                gutter: '.grid-gutter'
            }

            // * fix size
            // masonry: {
            //     columnWidth: 770,
            // 	gutter: 140,
            // 	isFitWidth: true
            // }
        });


        // bind filter click
        let filtersElem = document.querySelector('.filters-button-group');
        let filterBtns = filtersElem.querySelectorAll('button');
        
        filtersElem.addEventListener('click', function(e) {
        
        let filterValue = e.target.getAttribute('data-filter');
        iso.arrange({ filter: filterValue });
        
        e.target.classList.add('on');

        for (let i = 0; i < filterBtns.length; i++) {
            if(filterBtns[i] === e.target) continue;
            filterBtns[i].classList.remove('on');
        }
        });

    });


    /* 
        mobile Size Check 480
    */
    _this.mobileMode = () => {
        _this.setState({
            isMobile: true,
            filterOpen : false
        });
        document.querySelector('.filter').classList.add('fold');
    }

    _this.pcMode = () => {
        _this.setState({
            isMobile: false,
            filterOpen : true
        });
        document.querySelector('.filter').classList.remove('fold');
    }


    // 1. initial check
    let winWid = window.innerWidth;
    if(_this.state.isMobile){
        if(winWid > 480){
            _this.pcMode();
        }
    } else {
        if(winWid <= 480){
            _this.mobileMode();
        }
    }


    // 2. resize check 
    window.addEventListener('resize', function(){

        // work 컴포넌트를 메인에서도 사용하기때문에 sorting이 있을때만 실행되도록 체크
        // Main 은 sorting 이 없으므로 에러남
        let filterGroup = document.querySelector('.filters-button-group');
        if (filterGroup !== null){

            let winWid = window.innerWidth;

            if(_this.state.isMobile){
                if(winWid > 480){
                    _this.pcMode();
                }
            } else {
                if(winWid <= 480){
                    _this.mobileMode();
                }
            }
            
        }
    });

    // mobile select filter -> close
    let filtersElem = document.querySelector('.filters-button-group');
    filtersElem.addEventListener('click', () => {
        if(this.state.isMobile){
            this.filterToggle();
        }
    });


}


filterToggle = () => {
    if(this.state.filterOpen){
        this.setState({
            filterOpen : false
        });
        document.querySelector('.filter').classList.add('fold');
    } else {
        this.setState({
            filterOpen : true
        });
        document.querySelector('.filter').classList.remove('fold');
    }
}


  render(){
      return (
          <React.Fragment>

            <div id="notice">

              <div className="section_copy">
                  <p className="cate">Notice</p>
                  <p className="desc">
                      우리의 이야기가 궁금하신가요? <br/>
                      애드캡슐의 소식을 알려드립니다. 
                  </p>
              </div>


              <div className="inner_left">

                  <div className="filter">
                      <button className="filter_tg" onClick={this.filterToggle}>Filter Work<span className="arrow"></span></button>
                      <div className="filters-button-group">
                          <button className="on" data-filter="*">All</button>
                          <button data-filter=".news">News</button>
                          <button data-filter=".awards">Awrads</button>
                      </div>
                  </div>

                  <div className="item_wrap">

                      <div className="grid-sizer"></div>
                      <div className="grid-gutter"></div>
      
                      {/* NoticeListCmp 맵핑 */}
                      {this.state.listData.map((list,i) => {
                          return (
                              <NoticeListCmp 
                                  type={list.type} 
                                  title={list.title} 
                                  img={list.img} 
                                  cate={list.cate}
                                  date={list.date}
                                  routeLink={list.routeLink}
                                  key={i} 
                              />
                          )
                      })}
                      
                  </div>
              </div>

            </div>

          </React.Fragment>
      )
  }

}

export default Notice;
