import React from 'react';
import MobileCheck from '../js/mobileCheck.js';

import CounselorPc from './CounselorPc';
import CounselorMo from './CounselorMo';

class Counselor extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            isMobile : false
        }
    }

    componentWillMount(){
        MobileCheck(this);
    }

    render(){
        return (
            <React.Fragment>
                {this.state.isMobile ? <CounselorMo /> : <CounselorPc />}
            </React.Fragment>
        )
    }
}

export default Counselor;