import React, { Component } from 'react';
import imagesLoaded from 'imagesloaded';


class Loading extends Component {
  
  constructor(props){
    super(props);
  }
 
  componentDidMount(){
      // console.log('mount');
      this.loading();
  }

  componentDidUpdate(){
    // console.log('update');
    this.loading();
  }

  loading(){
      // imagesLoaded / Detect when images have been loaded.
      let imgLoad = imagesLoaded('img', function(){
      });

      // Loading Bar
      let loadLine = document.querySelector('.loadline');
      let imgLength = imgLoad.images.length;
      let loadCount = 0;

      imgLoad.on('progress', () => {
          loadCount++;
          let loadPercent = Math.floor((loadCount/imgLength)*100);
          loadLine.style.width = loadPercent+'%'
          // loadLine.textContent = 'Loading'+loadPercent+'%';
          // console.log(imgLength, loadCount, loadPercent);
      });

      imgLoad.on('always', () => {
          // console.log('loaded');
          setTimeout(()=>{
              loadLine.style.opacity = 0;
          },400);
          setTimeout(()=>{
              loadLine.style.width = 0;
          },800);
          setTimeout(()=>{
            loadLine.style.opacity = 1;
          },1200);
      });
  }

  render() {

    return (
      <React.Fragment>
        <div className="loadline"></div>
       </React.Fragment>
    );
  }
}

export default Loading;
