import React from 'react';
import { Link } from 'react-router-dom';

class NoticeBottomCmp extends React.Component {
    constructor(props){
        super(props);
    }

    render(){
        return (
            <React.Fragment>
                <div className="btn_wrap">
                    <Link to="/Notice" className="btn action wide">
                        목록으로
                    </Link>
                </div>
            </React.Fragment>
        )
    }
}

export default NoticeBottomCmp;