import React from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import Swiper from 'react-id-swiper';
import 'react-id-swiper/src/styles/css/swiper.css'

import { ViewTopCmp, PrjInfoCmp, PrjBottomCmp } from '../component';

import '../css/project.scss';

// project style import
import '../css/holistic.scss';

import projectData from './projectData.json';

// image import
import img1 from '../images/project/holistic/logo.png';
import img2 from '../images/project/holistic/img-ipad.png';
import img3 from '../images/project/holistic/img-ipad-01.png';
import img4 from '../images/project/holistic/img-ipad-02.png';
import img5 from '../images/project/holistic/img-ipad-03.png';
import img6 from '../images/project/holistic/img-ipad-04.png';
import img7 from '../images/project/holistic/img-ipad-05.png';
import img8 from '../images/project/holistic/img-ipad-06.png';
import img9 from '../images/project/holistic/img-01.png';
import img10 from '../images/project/holistic/img-02.png';
import img12 from '../images/project/holistic/bg-graph.png';
import img13 from '../images/project/holistic/invalid-name1.png';
import img14 from '../images/project/holistic/invalid-name2.png';
import img15 from '../images/project/holistic/img-03.png';
import img16 from '../images/project/holistic/img-04.png';
import img17 from '../images/project/holistic/tablet-01.png';
import img18 from '../images/project/holistic/tablet-02.png';
import img19 from '../images/project/holistic/tablet-03.png';
import img20 from '../images/project/holistic/tablet-04.png';
import img21 from '../images/project/holistic/tablet-05.png';
import img22 from '../images/project/holistic/tablet-06.png';
import img23 from '../images/project/holistic/tablet-07.png';
import img24 from '../images/project/holistic/tablet-08.png';
import img25 from '../images/project/holistic/solution-img.png';
import Video from '../images/project/holistic/video.mp4';

class HolisticPc extends React.Component {
    constructor(props){
        super(props);

        this.idx = 3;

        this.state = {
            type: projectData[this.idx].type,        
            cate: projectData[this.idx].cate,        
            title: projectData[this.idx].title,      
            desc: projectData[this.idx].desc,        
        }
    }

    componentDidMount(){
        AOS.init({});
    }

    render(){
        const params = {
            slidesPerView: 3,
            centeredSlides: true,
            loop: true,
            noSwiping: true,
            autoplay: {
                delay: 2500,
                disableOnInteraction: true
            }
        };

        return (
            <React.Fragment>
                <ViewTopCmp
                    type={this.state.type} 
                    cate={this.state.cate}
                    title={this.state.title} 
                    desc={this.state.desc} 
                />

                <div className="holisticWrap">
                    <div className="visualTop">
                        <div className="logo"><img src={img1} alt=""/></div>
                        <h2>Holistic<br />Balance counseling</h2>
                        <div className="ipad">
                            <div className="bg"><img src={img2} alt=""/></div>
                            <Swiper {...params}>
                                <div><img src={img3} alt=""/></div>
                                <div><img src={img4} alt=""/></div>
                                <div><img src={img5} alt=""/></div>
                                <div><img src={img6} alt=""/></div>
                                <div><img src={img7} alt=""/></div>
                                <div><img src={img8} alt=""/></div>
                            </Swiper>
                        </div>
                        <div className="info">
                            <div>
                                <div>Partner <p>AMORE PACIFIC</p></div>
                                <div>Date <p>2018.03.02</p></div>
                            </div>
                            <div>
                                <div>Mission <p>Tablet APP</p></div>
                                <div>LINK <p>App Store  /  Google Play</p></div>
                            </div>
                            <div>Challenge 
                                <p>
                                    설화수 브랜드 매장 내 뷰티 카운셀링 앱으로 차별화되고 개인화된 서비스 경험을 제공합니다.
                                    피부측정 브랜드 가치에 맞는 모던하고 고급스러운 룩과 타깃층에 맞게 트렌드를 반영한 레이아웃에 
                                    가로 스크롤링 페럴렉스 기법을 더해 컨텐츠와 이미지가 흐르듯이 자연스럽게 움직이는 UI를 구성함으로써
                                    브랜드 서비스 스토리텔링도 강화하였습니다.
                                </p>
                            </div>
                        </div>
                    </div>
                    <ol>
                        <li>
                            <h3>01<br />Check skin concerns</h3>
                            <p>
                                피부 타입 분석을 위해 사진을 찍고, 문진으로 피부 상태를 파악하여 카운셀링 및 솔루션을 제공
                                그리고 제품 추천까지 이어지는 설화수만의 특별함을 담아 제작하였습니다. 
                                또한, 매장에서만 사용되는 장치라는 특성 때문에 화면 내 심플한 텍스트 구성으로
                                시각적 피로감 최소화하고문진 항목을 세분화함으로써 콘텐츠 집중도를 높이고 깊이 있는 UX 제공합니다.
                            </p>
                            <div className="img1" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay=""><img src={img17} alt=""/></div>
                            <div className="img2" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay=""><img src={img18} alt=""/></div>
                            <div className="img3" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay=""><img src={img19} alt=""/></div>
                            <div className="img4" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay=""><img src={img20} alt=""/></div>
                            <div className="img5" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay=""><img src={img21} alt=""/></div>
                            <div className="img6" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay=""><img src={img22} alt=""/></div>
                            <div className="img7" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay=""><img src={img23} alt=""/></div>
                            <div className="img8" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay=""><img src={img24} alt=""/></div>
                        </li>
                        <li>
                            <h3>02<br />Results of counseling</h3>
                            <p>
                                문진 하나로 피부 상태, 나이, 감정까지 파악하여 매장에 있는 앤젤이 7세 주기 표를 고객에게 설명해 줍니다.
                                이러한 서비스를 통해 우리나라뿐만 아니라 세계 각국에 설화수만의 차별화된 뷰티 서비스를 넘어
                                아시아 뷰티를 널리 알리고자 했습니다.
                            </p>
                            <img src={img9} alt="" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="" />
                            <div className="img" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="1500"><img src={img10} alt=""/></div>
                        </li>
                        <li>
                            <h3>03<br />Solution</h3>
                            <p>
                                피부 타입 문진에 더불어 바라는 마음과 감정을 체크하여 설화수만의 특별한 5가지 향도 함께 추천해 드립니다.
                                이러한 향이 심신 안정과 밸런스를 유지하는데 도움을 줍니다.
                            </p>
                            <div className="txtImg"><img src={img25} alt=""/></div>
                            <div className="video_wrap" data-aos="fade-up">
                                <video id="Video" playsInline muted loop controls width="100%">
                                    <source src={Video} type="video/mp4" />
                                </video>
                            </div>
                        </li>
                        <li>
                            <h3>04<br />Five-type fragrance</h3>
                            <p>
                                1. Origin - &lt;동의보감&gt; 잡병편 향보 모티브로 하여 향의 문학적 스토리를 표현<br />
                                2. Visualization - 전통 색감을 통한 비쥬얼 스펙트럼 확보 - 오방색을 현대 감각으로 해석<br />
                                3. Ingredients - 자연 소재의 점진적 확장을 통해 부분이 아닌 전체를 바라보는 설화수의 철학 전달
                            </p>
                            <div className="img1" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay=""><img src={img12} alt=""/></div>
                            <div className="img2" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="1500"><img src={img13} alt=""/></div>
                            <div className="img3" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="1500"><img src={img14} alt=""/></div>
                            <div className="img4" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="2500"><img src={img15} alt=""/></div>
                            <div className="img5" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="3000"><img src={img16} alt=""/></div>
                        </li>
                    </ol>
                </div>

            <PrjBottomCmp/>

            </React.Fragment>
        )
    }
}

export default HolisticPc;