import React from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';

import { ViewTopCmp, PrjInfoCmp, PrjBottomCmp } from '../component';

import '../css/project.scss';

// project style import
import '../css/mwave.scss';

import projectData from './projectData.json';

// image import
import img1 from '../images/project/mwave/logo.png';
import img2 from '../images/project/mwave/line.png';
import img3 from '../images/project/mwave/top-macbook-mockup.png';
import img4 from '../images/project/mwave/top-iphone-mockup.png';
import img5 from '../images/project/mwave/img-txt.png';
import img6 from '../images/project/mwave/screen-01-1.png';
import img7 from '../images/project/mwave/screen-01-2.png';
import img8 from '../images/project/mwave/screen-01-3.png';
import img9 from '../images/project/mwave/screen-01-4.png';
import img10 from '../images/project/mwave/screen-01-5.png';
import img11 from '../images/project/mwave/screen-01-6.png';
import img12 from '../images/project/mwave/screen-02-1.png';
import img13 from '../images/project/mwave/screen-02-2.png';
import img14 from '../images/project/mwave/screen-02-3.png';
import img15 from '../images/project/mwave/screen-02-4.png';
import img16 from '../images/project/mwave/screen-02-5.png';
import img17 from '../images/project/mwave/screen-02-6.png';
import img18 from '../images/project/mwave/screen-02-7.png';
import img19 from '../images/project/mwave/screen-02-8.png';
import img20 from '../images/project/mwave/screen-02-9.png';
import img21 from '../images/project/mwave/screen-02-10.png';
import img22 from '../images/project/mwave/screen-02-11.png';
import img23 from '../images/project/mwave/screen-02-12.png';



class MwavePc extends React.Component {
    constructor(props){
        super(props);

        this.idx = 5;

        this.state = {
            type: projectData[this.idx].type,
            cate: projectData[this.idx].cate,
            title: projectData[this.idx].title,
            desc: projectData[this.idx].desc,
            mission: projectData[this.idx].mission,
            partner: projectData[this.idx].partner,
            launch: projectData[this.idx].launch,
            url: projectData[this.idx].url     
        }
    }

    componentDidMount(){
        AOS.init({});
    }

    render(){
        return (
            <React.Fragment>

                <ViewTopCmp
                    type={this.state.type} 
                    cate={this.state.cate}
                    title={this.state.title} 
                    desc={this.state.desc} 
                />

                <div className="MwaveWrap">
                    <div className="VisualTop">
                        <div className="logo"><img src={img1} alt=""/></div>
                        <div className="clear">
                            <div className="LeftTxt">
                                <h4>Mwave</h4>
                                <ul className="clear">
                                    <li>UI/UX<br />Brand Integration<br />Voting system</li>
                                    <li>Mobile Application<br />Mobile Website<br />PC Website</li>
                                </ul>
                                <h4>Challenge</h4>
                                <p>CJ degital music은 세계최고의 글로벌 미디어 그룹입니다. 하지만 잘 다듬어져 있는 브랜드 이미지에 비해 컨텐츠 및 서비스 들은 다소 산재되어 있어 동일한 사용자 경험을 제공하고 있지 못하고 있었습니다.</p>
                                <p>이에 저희는 각기 흩어져 있는 서비스를 병합하고 나누어 동일한 사용자 경험을 제공하였습니다.</p>
                                <p>또한 모든 컨텐츠 및 서비스를 컴포넌트화 하여 국내사용자 뿐만이 아니라 다른나라에서도 동일한 사용자 경험 / 다른 서비스를 이질감 없이 제공합니다.</p>
                            </div>
                            <div className="RightImg">
                                <img src={img3} alt=""/>
                                <img src={img4} alt=""/>
                            </div>
                        </div>

                        <ul className="BottomTxt clear">
                            <li>
                                mission
                                <p>MWave Online 통합</p>
                            </li>
                            <li>
                                partner
                                <p>CJ 디지털 뮤직</p>
                            </li>
                            <li>
                                link
                                <p>http://www.mwave.me / App store / Google Play</p>
                            </li>
                        </ul>
                    </div>
                    <div>
                        <img src={img5} alt=""/>
                    </div>
                    <div className="UxPoint">
                        <div className="txt">
                            <h4>UX Point</h4>
                            <p>M Wave의 콘텐츠는 팬미팅, 쇼케이스, 투표 그리고 쇼핑에 이르는 라이프스타일 전반의 다채롭고 흥미로운 볼거리로 구성되어있습니다.
                                상세화면에서 양질의 콘텐츠를 소비한 뒤 해당채널의 링크와 관련 상품, 관련 콘텐츠등을 제공하여 지속적으로 플랫폼 안에서의<br /> 끊임없는 경험을 제공합니다.</p>
                        </div>
                        <div className="ObjectWrap">
                            <div className="object01" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="500"><img src={img6} alt=""/></div>
                            <div className="object02" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="500"><img src={img7} alt=""/></div>
                            <div className="object03" data-aos="fade-up" data-aos-duration="500" data-aos-easing="ease-in-out"><img src={img8} alt=""/></div>
                            <div className="object04" data-aos="fade-up" data-aos-duration="500" data-aos-easing="ease-in-out"><img src={img9} alt=""/></div>
                            <div className="object05" data-aos="fade-up" data-aos-duration="500" data-aos-easing="ease-in-out"><img src={img10} alt=""/></div>
                            <div className="object06" data-aos="fade-up" data-aos-duration="500" data-aos-easing="ease-in-out"><img src={img11} alt=""/></div>
                        </div>
                    </div>
                    <div className="UiElement">
                        <div className="txt">
                            <h4>UI Element</h4>
                            <p>MWave의 모든 컨텐츠는 Component 화하여 PC,Mobile 환경에 맞추어 일관성 있게 노출됩니다.<br />
                                각 서비스에 맞는 컨텐츠를 관리자가 쉽게 조합하고 배열 함으로서 미주,유럽,일본,중국,동남아시아등의 해외 체인에서도 동일한 MWave만의 경험을 제공합니다.</p>
                        </div>
                        <p className="bigTxt">Music<br />makes one!</p>
                        <div className="ObjectWrap">
                            <div className="object01" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="800"><img src={img12} alt=""/></div>
                            <div className="object02" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="800"><img src={img13} alt=""/></div>
                            <div className="object03" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="800"><img src={img14} alt=""/></div>
                            <div className="object04" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="800"><img src={img15} alt=""/></div>
                            <div className="object05" data-aos="fade-up" data-aos-duration="1000" data-aos-easing="ease-in-out" data-aos-delay="800"><img src={img16} alt=""/></div>
                            <div className="fl">
                                <div className="object11" data-aos="fade-up" data-aos-duration="800" data-aos-easing="ease-in-out"><img src={img17} alt=""/></div>
                                <div className="object13" data-aos="fade-up" data-aos-duration="800" data-aos-easing="ease-in-out"><img src={img19} alt=""/></div>
                                <div className="object15" data-aos="fade-up" data-aos-duration="800" data-aos-easing="ease-in-out"><img src={img21} alt=""/></div>
                            </div>
                            <div className="fl">
                                <div className="object12" data-aos="fade-up" data-aos-duration="800" data-aos-easing="ease-in-out"><img src={img18} alt=""/></div>
                                <div className="object14" data-aos="fade-up" data-aos-duration="800" data-aos-easing="ease-in-out"><img src={img20} alt=""/></div>
                                <div className="object16" data-aos="fade-up" data-aos-duration="800" data-aos-easing="ease-in-out"><img src={img22} alt=""/></div>
                                <div className="object17" data-aos="fade-up" data-aos-duration="800" data-aos-easing="ease-in-out"><img src={img23} alt=""/></div>
                            </div>
                        </div>
                    </div>
                </div>

                <PrjBottomCmp/>

            </React.Fragment>
        )
    }
}

export default MwavePc;