import React from 'react';
import MobileCheck from '../js/mobileCheck.js';

import HolisticPc from './HolisticPc';
import HolisticMo from './HolisticMo';

class Holistic extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            isMobile : false
        }
    }

    componentWillMount(){
        MobileCheck(this);
    }

    render(){
        return (
            <React.Fragment>
                {this.state.isMobile ? <HolisticMo /> : <HolisticPc />}
            </React.Fragment>
        )
    }
}

export default Holistic;