import React from 'react';
import MobileCheck from '../js/mobileCheck.js';

import MwavePc from './MwavePc';
import MwaveMo from './MwaveMo';

class Mwave extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            isMobile : false
        }
    }

    componentWillMount(){
        MobileCheck(this);
    }

    render(){
        return (
            <React.Fragment>
                {this.state.isMobile ? <MwaveMo /> : <MwavePc />}
            </React.Fragment>
        )
    }
}

export default Mwave;