import React, { Component } from 'react';

import '../css/contact.scss'

import ContactProject from './ContactProject';
import ContactHire from './ContactHire';
import { Spring } from 'react-spring';

class Contact extends Component {

  constructor(props){
    super(props);
    this.state = {
      cntPrj: false,
      cntHire: false,
    };

  }


  componentDidMount(){

    // page scroll top
    window.scrollTo({'top':0});

    // layout style
    this.container = document.getElementById('container');
    this.container.classList.add('type_clear');
    document.querySelector('footer').classList.add('simple');

  }

  componentWillUnmount(){
    // layout style
    this.container = document.getElementById('container');
    this.container.classList.remove('type_clear');
    document.querySelector('footer').classList.remove('simple');

  }

  cntView(e){
    e.preventDefault();
    const targetId = e.target.getAttribute('id');
    const cntPrjA = document.querySelector('#cntPrj');
    const cntHireA = document.querySelector('#cntHire');


    if(targetId == 'cntPrj'){
      cntHireA.classList.remove('on');
      
      // toggle
      if(e.target.classList.contains('on')){
        e.target.classList.remove('on');
        this.setState({
          cntPrj : false
        });

      } else {
        e.target.classList.add('on');
        this.setState({
          cntPrj : true,
          cntHire : false
        });
      }
    } else {
      cntPrjA.classList.remove('on');

      if(e.target.classList.contains('on')){
        e.target.classList.remove('on');
        this.setState({
          cntHire : false
        });

      } else {
        e.target.classList.add('on');
        this.setState({
          cntPrj : false,
          cntHire : true
        });
      }

    }

  }

  cntPrev(e){
    e.preventDefault();
    // const cntA = document.querySelector('.lkwrap').querySelector('a');
    const cntPrjA = document.querySelector('#cntPrj');
    const cntHireA = document.querySelector('#cntHire');
    
    cntPrjA.classList.remove('on');
    cntHireA.classList.remove('on');

    this.setState({
      cntPrj : false,
      cntHire : false
    });
  }


  render() {
    return (
      <React.Fragment>

        <div id="contact">

          <div className="section_copy">
          
              <div className="arrow light">
                  <span></span>
                  <span></span>
                  <span></span>
              </div>

              <p className="cate">Contact</p>
              <p className="desc">
                애드캡슐에 무슨일로 오셨나요? <br/>
                당신의 이야기를 자유롭게 이야기해주세요.
              </p>

          </div>

          {/* 컴포넌트로 뽑기 */}
          <div className="cnt_link">
            <div className="lkwrap">
              <a href="#" id="cntPrj" onClick={this.cntView.bind(this)}>프로젝트를 의뢰하고 싶어요</a>
            </div>

            {this.state.cntPrj ? 
            <Spring 
              from={{opacity:0, transform:'translateX(-50px)'}} 
              to={{opacity:1, transform:'translateX(0)'}}
              config={{duration:300}}>
              {styles =>
                <div style={styles} className="cnt_project">

                  <div className="cnt_head">
                    <a href="#" className="prev" onClick={this.cntPrev.bind(this)}></a>
                    <p className="loc_tit">
                      프로젝트를 의뢰하고 싶어요
                    </p>
                  </div>
                  <ContactProject /> 
                </div>
              }
            </Spring>
            : null}

            <div className="lkwrap">
              <a href="#" id="cntHire" onClick={this.cntView.bind(this)}>함께 성장하고 싶어요</a>
            </div>
            
            {this.state.cntHire ? 
            <Spring 
              from={{opacity:0, transform:'translateX(-50px)'}} 
              to={{opacity:1, transform:'translateX(0)'}}
              config={{duration:300}}>
              {styles =>
                <div style={styles} className="cnt_project">

                  <div className="cnt_head">
                    <a href="#" className="prev" onClick={this.cntPrev.bind(this)}></a>
                    <p className="loc_tit">
                      함께 성장하고 싶어요
                    </p>
                  </div>
                  <ContactHire /> 
                </div>
              }
            </Spring>
            : null}

          </div>
          

        </div>

      </React.Fragment>
    );
  }
}

export default Contact;
