import React, { Component } from 'react';
import './css/index.scss';

// Router
import { Route, Switch } from 'react-router-dom';

import Header from './layout/Header';
import Footer from './layout/Footer';
import Loading from './layout/Loading';

// guide 확인용
import Guide from './layout/Guide';

// import Main from './layout/Main';
// import Work from './layout/Work';
// import View from './layout/View';

// 하나의 디렉토리에서 import 하기 위해 index.js 에서 export 설정 필요
import { Main, About, Work, Notice, Contact, Dtp } from './layout';

// projects import
import { 
  //Project1, 
  //Project2, 
  //Project3, 
  //Project4, 
  //Project5, 

  Asiana,
  Mwave,
  LotteDept,
  Holistic,
  Counselor,
  BeautyPoint,

  // template
  Template1,
  Template2,
  Template3,
  Template4

} from './projects';


// notice import
import { 
  NoticeView1,
  NoticeView2,
  NoticeView3,
  NoticeView4,
  NoticeView5,
  NoticeView6,
  NoticeView7,
  NoticeView8
} from './notice';


import Form from './js/form.js';


class App extends Component {

  componentDidMount(){
    // contact path 일때 FooterSimple로 보여주기

    // 공통으로 쓸 js 를 이렇게 불러도 되는지
      Form();
  }


  render() {
    return (
      <React.Fragment>

      <Loading/>

      <div id="container">
        <Header />

        <div className="inner">
            {/* <Switch onUpdate={() => window.scrollTo(0, 0)} history={browserHistory}> */}

              <Switch>
                <Route exact path="/" component={Main} />
                <Route path="/Guide" component={Guide} />
                <Route path="/Work" component={Work} />
                <Route path="/About" component={About} />
                <Route path="/Notice" component={Notice} />
                <Route path="/Contact" component={Contact} />
                <Route path="/Dtp" component={Dtp} />
                <Route path="/Counselor" component={Counselor} />
                <Route path="/Asiana" component={Asiana} />
                <Route path="/Holistic" component={Holistic} />
                <Route path="/LotteDept" component={LotteDept} />
                <Route path="/Mwave" component={Mwave} />
                <Route path="/BeautyPoint" component={BeautyPoint} />

                <Route path="/Template1" component={Template1} />
                <Route path="/Template2" component={Template2} />
                <Route path="/Template3" component={Template3} />
                <Route path="/Template4" component={Template4} />

                <Route path="/NoticeView1" component={NoticeView1} />
                <Route path="/NoticeView2" component={NoticeView2} />
                <Route path="/NoticeView3" component={NoticeView3} />
                <Route path="/NoticeView4" component={NoticeView4} />
                <Route path="/NoticeView5" component={NoticeView5} />
                <Route path="/NoticeView6" component={NoticeView6} />
                <Route path="/NoticeView7" component={NoticeView7} />
                <Route path="/NoticeView8" component={NoticeView8} />

                

                {/* <Route exact path={`${process.env.PUBLIC_URL}/`} component={Main} />
                <Route path={`${process.env.PUBLIC_URL}/Guide`} component={Guide} />
                <Route path={`${process.env.PUBLIC_URL}/Work`} component={Work} />
                <Route path={`${process.env.PUBLIC_URL}/Contact`} component={Contact} />
                <Route path={`${process.env.PUBLIC_URL}/Project1`} component={Project1} />
                <Route path={`${process.env.PUBLIC_URL}/Project2`} component={Project2} />
                <Route path={`${process.env.PUBLIC_URL}/Project3`} component={Project3} />
                <Route path={`${process.env.PUBLIC_URL}/Project4`} component={Project4} />
                <Route path={`${process.env.PUBLIC_URL}/Project5`} component={Project5} />
                <Route path={`${process.env.PUBLIC_URL}/BeautyPoint`} component={BeautyPoint} /> */}

              </Switch>

          </div>
          
          <Footer/>

      </div>
      </React.Fragment>
    );
  }
}

export default App;
